.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App{
  overflow: scroll;
  height: 100%;
  width: 100%;
}
.Paragraphe {
  font-family: "Gentium";
  font-weight: "regular";
  font-size: larger;
}
.right {
  text-align: right;
}
.Awani {
  font-family: "Awani" !important;
}
.Gentium {
  font-family: "Gentium" !important;
}
.Andika {
  font-family: "Andika" !important;
}
.NotoNaskhArabic{
  font-family: "NotoNaskhArabic" !important;
}

.headerBar {
  z-index: 1;
  background: white;
  height: 60px;
  box-shadow: 0px 1px 0px;
  border: 1px;
  align-items: center;
  display: flex;
}

.h{
  font-weight: 800;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.styleModal {
  position: 'absolute';
  top: '50%';
  left: '50%';
  transform: 'translate(-50%, -50%)';
  width: 400;
  bgcolor: 'background.paper';
  border: '2px solid #000';
  boxShadow: 24;
  p: 4;
};